} */.moviecard{
    background-color: rgb(27, 47, 58);
    width: 300px;
    overflow: hidden;
    margin: 1rem;
    position: relative;
    border-radius:3px;
    box-shadow:3px 3px 3px rgba(0,0,0,0.1);
}

.moviecard__image > img{max-width:100%}

.moviecard__details
{display: flex;
padding: 1rem;
align-items: center;
justify-content: space-between;
}
.movie__details > h4{
    margin: 0;
}
.moviecard__overview{
    position: absolute;
    bottom:0;
    left: 0;
    right: 0;
    overflow:auto;
    max-height: 100%;
    background: rgb(85, 89, 97);
    padding: 1rem;
    transform: translateY(100%);
    transition: transform 0.3s ease-in-out;
}
span{
    background-color: rgb(14, 33, 70);
    padding: 10px;
    border-radius:10px;
}


.moviecard:hover .moviecard__overview{
    transform: translateY(0%);
}